.music {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: end;
  }

  #music-toggle {
    background-color: rgb(30, 233, 7);
    color: rgb(0, 10, 10);
    border-radius: 2em;
    cursor: pointer;
    position: fixed;
  }
  #music-toggle:hover {
    transform: scale(1.20, 1.20);
  }

  #next-music{
    background-color: rgb(30, 233, 7);
    color: rgb(0, 10, 10);
    border-radius: 2em;
    cursor: pointer;
    position: fixed;
  }

  .change-music {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: end;
  }





  /* Style for modal container */
.modal-container {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  /* Style for modal content */
  .modal-content {
    background-color: #fff;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #888;
    max-width: 600px;
  }
  
  /* Style for close button */
  .close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .close-button:hover {
    color: black;
  }
  #about-button{
    height: 45px;
    cursor: pointer;
  }